<template>
  <div class="data-share">
    <div class="content-header">
      <bread-crumb current-level="数据分享" />
    </div>
    <two-column-layout>
      <template slot="left">
        <gs-button
          v-if="sourceGroupTree.length"
          class="add-group-btn"
          type="primary"
          icon="plus"
          @click="openAddGroupModal"
        />
        <side-tree
          ref="sideTree"
          slot="left"
          :loading="loadingGroup"
          :active="activeGroup"
          :actions="actions"
          :tree-props="treeProps"
          empty-btn-text="点击创建分组"
          preicon=""
          search-placeholder="请输入分组名称"
          @search="handleGroupSearch"
          @node-click="handleSelectGroup"
          @node-action="handleNodeAction"
          @create-category="openAddGroupModal"
        />
      </template>
      <template slot="right">
        <router-view
          :active-group="activeGroup"
        />
      </template>
    </two-column-layout>
    <edit-group
      v-model="editGroupModalVisible"
      :data="changeGroupInfo"
      @refresh="fetchGroupList"
    />
  </div>
</template>
<script>
import TwoColumnLayout from 'src/components/two-column-layout';
import BreadCrumb from 'src/components/breadcrumb';
import sideTree from 'src/components/side-tree';
import * as service from 'src/service/data-share/index';
import {list2tree} from 'src/utils';
import editGroup from './edit-group';
import {mapState, mapActions} from 'vuex';
import './style.scss';
const ACTIONS = data => {
  return [
    {
      label: '修改组名',
      value: 0,
      disabled: !data.parentGuid || data.guid === '0'
    },
    {
      label: '删除分组',
      value: 1,
      disabled: !data.parentGuid || data.guid === '0'
    }
  ];
};
export default {
  components: {
    TwoColumnLayout,
    BreadCrumb,
    sideTree,
    editGroup
  },
  data() {
    return {
      tableList: [],
      loadingGroup: false,
      activeGroup: {},
      actions: ACTIONS,
      groupTree: [],
      editGroupModalVisible: false,
      changeGroupInfo: null
    };
  },
  computed: {
    ...mapState('datashare', {
      sourceGroupTree: 'sourceGroupTree'
    }),
    treeProps() {
      return {
        data: this.groupTree,
        'node-key': 'guid',
        'default-all-expand': true,
        props: {
          label: 'name',
          value: 'guid'
        },
        filterNodeMethod: (val, data) => {
          if (!val) {
            return true;
          }
          return data.name.includes(val);
        }
      };
    }
  },
  created() {
    this.fetchGroupList().then(_ => {
      if (this.$route.query.groupId) {
        const activeGroup = this.sourceGroupTree.find(item => item.guid === this.$route.query.groupId);
        if (activeGroup) {
          this.activeGroup = activeGroup;
        } else {
          this.activeGroup = this.groupTree[0];
        }
      } else {
        this.activeGroup = this.groupTree[0];
        this.$route.query.groupId = this.groupTree[0].guid;
      }
    });
    window.that = this;
  },
  methods: {
    ...mapActions('datashare', [
      'getGroupList'
    ]),
    handleGroupSearch(value) {
      this.$refs.sideTree.$refs.tree.filter(value);
    },
    handleSelectGroup(value) {
      if (this.$route.path !== '/main/data-share') {
        this.$router.replace(`/main/data-share`);
        // window.location.href = '/#/main/data-share/list';
      }
      this.activeGroup = value;
      this.$router.replace({
        query: {
          groupId: value.guid
        }
      });
    },
    handleNodeAction(data, type) {
      if (type === 0) {
        this.changeGroupInfo = data;
        this.editGroupModalVisible = true;
      }
      if (type === 1) {
        this.deleteGroup(data);
      }
    },
    openAddGroupModal() {
      this.changeGroupInfo = null;
      this.editGroupModalVisible = true;
    },
    fetchGroupList() {
      this.loadingGroup = true;
      return this.getGroupList().then(data => {
        this.loadingGroup = false;
        this.groupTree = list2tree(this.sourceGroupTree, 'guid', 'parentGuid');
      }).catch(e => {
        this.loadingGroup = false;
      });
    },
    deleteGroup({guid}) {
      this.$Modal.confirm({
        title: '删除分组',
        content: '确定删除该分组，删除后将无法恢复',
        onOk: () => {
          service.deleteGroup(guid).then(data => {
            this.$Message.success(`删除成功`);
            this.fetchGroupList();
            this.activeGroup = this.groupTree[0];
          });
        }
      });
    }
  }
};
</script>



