<template>
  <gs-modal
    v-model="visible"
    class="edit-group"
    @cancel="$emit('input', false)"
    @confirm="confirm"
    @visible-change="visibleChange"
  >
    <gs-form
      ref="form"
      :model="form"
      :rules="rules"
      label-width="60px"
    >
      <gs-form-item
        label="名称"
        prop="name"
      >
        <gs-input
          v-model="form.name"
          placeholder="请输入分组名称"
        />
      </gs-form-item>
      <gs-form-item
        label="描述"
        prop="description"
      >
        <gs-textarea
          v-model="form.description"
        />
      </gs-form-item>
    </gs-form>
  </gs-modal>
</template>
<script>
import * as service from 'src/service/data-share/index';
export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      visible: false,
      form: {
        name: '',
        description: ''
      },
      rules: {
        name: [{required: true, message: '请输入分组名称', trigger: 'blur'}]
      }
    };
  },
  watch: {
    value(val) {
      this.visible = val;
    }
  },
  methods: {
    confirm() {
      this.$refs.form.validate(valid => {
        if (valid) {
          if (this.data && this.data.guid) {
            service.editGroup({
              groupId: this.data.guid,
              params: this.form
            }).then(data => {
              this.$Message.success('修改成功');
              this.$emit('refresh');
              this.$emit('input', false);
            });
          } else {
            service.addGroup(this.form).then(data => {
              this.$Message.success('添加成功');
              this.$emit('refresh');
              this.$emit('input', false);
            });
          }
        }
      });
    },
    visibleChange(val) {
      if (val) {
        this.$refs.form.resetFields();
        if (this.data && this.data.guid) {
          const {name, description} = this.data;
          this.form = {name, description};
        }
      }
    }
  }
};
</script>



